import { createStore } from 'vuex'
import axios from "axios"

export default createStore({
  state: {
    // 初始配置信息
    InitIndexData: {},
  },
  getters: {
  },
  mutations: {
    GetInitIndexData(){
      axios.get("/v1/api/env/data").then((res) => {
        this.state.InitIndexData = res.data.data
      })
    },
  },
  actions: {
    // 执行 changeData 方法
    changeData(){
      this.commit("GetInitIndexData")
    },
  },
  modules: {
  }
})
